@use "setting" as *;

.p-form__text {
	-moz-appearance: none;
	-webkit-appearance: none;
	appearance: none;
	background-color: transparent;
	background-image: none;
	border: 1px solid rgba(0, 0, 0, 0.16);
	border-radius: 0;
	color: inherit;
	font-family: inherit;
	font-size: 1em;
	padding: 0.65em 0.8em 0.65em 1.5em;
	width: 100%;
}

.p-form__text:focus {
	border: 1px solid rgba(0, 0, 0, 0.32);
	box-shadow: none;
	outline: none;
}

.p-form__text::placeholder,
.p-form__textarea::placeholder {
	font-size: rem(12);
}


.p-form__submit {
	-webkit-appearance: none;
	background-color: rgba(0, 0, 0, 0.32);
	background-image: none;
	border: none;
	border-radius: 0;
	color: #fff;
	cursor: pointer;
	display: inline-block;
	font-size: 1em;
	// margin: 0 0 1em;
	padding: 0.6em 2em;
	text-decoration: none;
}

.p-form__submit:hover,
.p-form__submit:focus {
	outline: none;
}

.p-form__submit::-moz-foucus-inner {
	border: none;
	padding: 0;
}

.p-form__textarea {
	-moz-appearance: none;
	-webkit-appearance: none;
	appearance: none;
	background-color: transparent;
	background-image: none;
	border: 1px solid rgba(0, 0, 0, 0.16);
	border-radius: 0;
	color: inherit;
	font-family: inherit;
	font-size: 1em;
	height: rem(240);
	padding: 0.65em 0.8em 0.65em 1.5em;
	width: 100%;
  resize: vertical;
}

.p-form__textarea:focus {
	border: 1px solid rgba(0, 0, 0, 0.32);
	box-shadow: none;
	outline: none;
}

.p-form__radio {
	display: none;
}

.p-form__radio + span {
	cursor: pointer;
	display: inline-block;
	margin: 0 0.2em 0 0;
	padding: 0 0 0 2em;
	position: relative;
}

.p-form__radio + span::before {
	-webkit-transform: translateY(-50%);
	background: #fff;
	border: 1px solid $color-form-border;
	border-radius: 50%;
	content: "";
	display: block;
	height: 16px;
	left: 0;
	position: absolute;
	top: 50%;
	transform: translateY(-50%);
	width: 16px;
}

.p-form__radio + span::after {
	-webkit-transform: translateY(-50%);
	background: $color-form-marker;
	border: 1px solid transparent;
	border-radius: 50%;
	content: "";
	height: 8px;
	left: 4px;
	opacity: 0;
	padding: 2px;
	position: absolute;
	top: 50%;
	transform: translateY(-50%);
	transition: all 0.3s ease 0s;
	width: 8px;
}

.p-form__radio:checked + span::after {
	opacity: 1;
}

.p-form__check {
	display: none;
}

.p-form__check + span {
	cursor: pointer;
	display: inline-block;
	margin: 0 0.2em 0;
	padding: 0 0 0 2em;
	position: relative;
}

.p-form__check + span::before {
	-webkit-transform: translateY(-50%);
	background: #fff;
	border: 1px solid $color-form-border;
	border-radius: 2px;
	content: "";
	display: block;
	height: 1em;
	left: 0;
	position: absolute;
	top: 50%;
	transform: translateY(-50%);
	width: 1em;
}

.p-form__check + span::after {
	-webkit-transform: translateY(-50%) rotate(-45deg);
	border-bottom: 3px solid $color-form-marker;
	border-left: 3px solid $color-form-marker;
	content: "";
	display: block;
	height: 0.6em;
	left: 0;
	margin-top: -0.2em;
	opacity: 0;
	position: absolute;
	top: 50%;
	transform: translateY(-50%) rotate(-45deg);
	transition: all 0.3s ease 0s;
	width: 1em;
}

.p-form__check:checked + span::after {
	opacity: 1;
}